<template>
  <div id="common">
    <div class="phone-viewport">
      <!-- Toolbar -->
      <md-toolbar class="md-accent">
        <md-button class="md-icon-button" @click="toggleLeftSidenav">
          <md-icon>menu</md-icon>
        </md-button>
  
        <h2 class="md-title" style="flex: 1">Tesla Model 3 Configurator</h2>
        <md-button class="md-icon-button" id="custom" @click="openDialog('dialog1')">
          <md-icon>info</md-icon>
        </md-button>
      </md-toolbar>
  
      <!-- Sidenav -->
      <md-sidenav class="md-left" ref="leftSidenav" @open="open('Left')" @close="close('Left')">
        <md-toolbar class="md-large">
          <div class="md-toolbar-container">
            <h3 class="md-title">Model 3 News HK</h3>
          </div>
        </md-toolbar>
        <md-list>
          <md-list-item @click.native="goToModel3">
            <md-icon>directions_car</md-icon>
            <span>Model 3 Configurator</span>
          </md-list-item>
          <md-divider></md-divider>
          <md-list-item @click.native="goToChargers">
            <md-icon>battery_charging_full</md-icon>
            <span>Superchargers</span>
          </md-list-item>
          <md-divider></md-divider>
          <md-list-item @click.native="goToNews">
            <md-icon>speaker_notes</md-icon>
            <span>News</span>
          </md-list-item>
          <md-divider></md-divider>
          <md-list-item href="https://www.youtube.com/channel/UCuQk1kqKYPHadq8CRlgAsoQ">
            <md-icon>video_library</md-icon>
            <span>Tesla Talk</span>
          </md-list-item>
        </md-list>
        <p>warren10hk Build 20170808</p>
      </md-sidenav>
    </div>
  
    <!-- Dialog -->
    <md-dialog md-open-from="#custom" md-close-to="#custom" ref="dialog1">
      <md-dialog-title>Cost Structure</md-dialog-title>
  
      <md-dialog-content>
        <div>
          <b>Model 3</b>
        </div>
        <div>Base Model: $35,000 (HK$290,500)</div>
        <div>Long Range Battery: $9,000 (HK$69,900)</div>
        <div>Color: $1,000 (HK$7,800)</div>
        <div>Wheels: $1,500 (HK$11,600)</div>
        <div>Premium Package: $5,000 (HK$38,800)</div>
        <div>Enhanced Autopilot: $5,000 (HK$38,800)</div>
        <div>Full Self-Driving Capability: $3,000 (HK$23,300)</div>
        <hr>
        <div>
          <b>Tax</b>
        </div>
        <div>First HK$150,000: 40%</div>
        <div>Next HK$150,000: 75%</div>
        <div>Next HK$200,000: 100%</div>
        <div>Remaining: 115%</div>
        <div>Tax Waived: $97,500</div>
      </md-dialog-content>
  
      <md-dialog-actions>
        <md-button class="md-primary" @click="closeDialog('dialog1')">Ok</md-button>
      </md-dialog-actions>
    </md-dialog>
  </div>
</template>

<script>
export default {
  name: 'common',
  methods: {
    openDialog(ref) {
      this.$refs[ref].open()
    },
    closeDialog(ref) {
      this.$refs[ref].close()
    },
    onOpen() {
      console.log('Opened')
    },
    onClose(type) {
      console.log('Closed', type)
    },
    toggleLeftSidenav() {
      this.$refs.leftSidenav.toggle()
    },
    closeRightSidenav() {
      this.$refs.rightSidenav.close()
    },
    open(ref) {
      console.log('Opened: ' + ref)
    },
    close(ref) {
      console.log('Closed: ' + ref)
    },
    goToModel3: function () {
      this.$router.push(`/Model3`)
      this.$refs.sidenav.close() // close the panel
    },
    goToChargers: function () {
      this.$router.push(`/Chargers`)
      this.$refs.sidenav.close() // close the panel
    },
    goToNews: function () {
      this.$router.push(`/News`)
      this.$refs.sidenav.close() // close the panel
    }
  }
}
</script>

<style>

</style>



